@import '../../../scss/setting/style.scss';



.wrap {
    max-width: 100%;

    &--slide {
        padding: 94px 0px 112px 0px;
        margin: 0 120px;
    }

    &--title {
        margin: 10px 0;
        font-weight: 600;
        font-size: 23px;
        line-height: 29px;
        color: color(color-pseudo);
        display: flex;
        justify-content: space-between;



        &__paragraph {
            font-weight: 600;
        }

        &__arrow {
            color: color(color-arrow);
            display: flex;
            cursor: pointer;

                &svg:first-child {
                    margin-right: 14px;

                }
        }
    }

}

.swiper {
    width: 100%;
    height: 100%;

    &.wrapper {
        margin: 0 auto;

    }

    &-slide {
        background: color(background-body);
        cursor: pointer;

        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        flex-direction: column;
        transition: ease all .8s;
        overflow: hidden;

      

        & img {
            transition: ease all .8s;
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transform: translateX(0);
            transition: all .3s ease-in-out;
            &:hover {
                img {
                    transform: translateX(1.2);
                }
            }
        }

        &-tag {
            position: absolute;
            top: 0;
            left: 50%;
            padding: 6px 10px 7px;
            font-size: 13px;
            line-height: 1;
            color: color(color-button);
            background-color: color(background-button);
            font-family: Montserrat, sans-serif;
            font-weight: 600;
            text-transform: capitalize;
            z-index: 5;
            -webkit-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
            transform: translateX(-50%);
            cursor: default;
        }
    }

    &-product {
        background-color: color(color-white);
        padding: 24px 0 30px;
        display: flex;
        justify-content: space-between;
        font-family: "Montserrat", sans-serif;
        &-price {
            display: flex;
            justify-content: space-between;
            font-size: 18px;
            font-weight: 600;

            &-discount {
                margin-right: 10px;
                text-decoration: line-through;
                color: color(color-primary);
            }

            .product-price {
                color: color(color-slider);
            }
        }

        &-categories  {
            text-align: left;
            & h5 {
                margin-bottom: 0;
            }
            & h5 a {

                font-size: 17px;
                text-decoration: none;
                color: color(color-product);
                font-weight: 600;
            }
        }

        &-categories a {
            vertical-align: top;
            font-size: 13px;
            color: color(color-price);
            font-weight: 400;
            text-decoration: none;
            transition: all 0.4s ease;
            font-family: "Poppins", sans-serif;

            &:hover {

                color: color(color-pseudo);
            }
        }
    }
}